.stars {
  display: flex;

  &__star {
    height: 16px;
    width: 16px;
    background-image: url(../images/star.svg);
    background-repeat: no-repeat;
    background-position: center;

    // &:not(:last-child) {
    //   margin-right: 4px;
    // }
  }

  @for $stars-number from 1 through 5 {
    &--#{$stars-number} :nth-child(-n + #{$stars-number}) {
      background-image: url(../images/star-active.svg);
    }
  }
}
