.card {
  box-sizing: border-box;
  margin: 0;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: $card-width;
  padding: 32px 16px 16px;
  scroll-behavior: smooth;

  &__img {
    margin: 0 auto;
    display: block;
    width: 160px;
    height: 134px;
  }

  &__title {
    margin: 40px 0 0;
    color: $main-accent;
    font-size: 12px;
    font-weight: 500;
    line-height: 18px;
  }

  &__code {
    margin: 4px 0 0;
    color: $secondary;
    font-size: 10px;
    font-weight: 400;
    line-height: 14px;
  }

  &__rating {
    margin: 16px 0 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__review {
    margin: 0;
    font-size: 10px;
    font-weight: 400;
    line-height: 14px;
  }

  &__price {
    display: flex;
    width: 166px;
    justify-content: space-between;
    align-items: flex-end;
    margin: 24px 0 0;

    &-title {
      margin: 0;
      color: $secondary;
      font-size: 12px;
      font-weight: 400;
      line-height: 18px;

      &:hover {
        color: #34568b;
      }
    }

    &-value {
      margin: 0;
      color: $main-accent;
      font-size: 16px;
      font-weight: 700;
      line-height: 18px;
    }
  }

  &__buttom-link {
    display: flex;
    box-sizing: border-box;
    justify-content: center;
    text-align: center;
    align-items: center;
    margin: 16px 0 0;
    text-decoration: none;
    text-transform: uppercase;
    height: 40px;
    border-radius: 5px;
    border: 1px solid $blue-accent;
    background: $blue-accent;
    color: $white;
    font-size: 14px;
    font-weight: 700;
    line-height: 16px;

    &:hover {
      background-color: $white;
      border-color: $blue-accent;
      color: $blue-accent;
    }
  }

  &:hover {
    transition: 300ms;
    transform: scale(120%);
  }
}
